'use client';

import { FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Dialog, DialogContent, DialogFooter, DialogHeader, DialogTitle, DialogDescription,
} from '@/components/ui/dialog';

interface SatuanFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (e?: FormEvent) => void;
    isSaving: boolean;
    formData: {
        nama_satuan: string;
    };
    setFormData: (data: any) => void;
    isEdit: boolean;
}

export default function SatuanFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    isSaving,
    formData,
    setFormData,
    isEdit
}: SatuanFormDialogProps) {
    
    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[400px]">
                <DialogHeader>
                    <DialogTitle>{isEdit ? 'Edit Satuan' : 'Tambah Satuan Baru'}</DialogTitle>
                    <DialogDescription>
                        Satuan digunakan untuk menghitung stok dan porsi.
                    </DialogDescription>
                </DialogHeader>
                
                <form onSubmit={onSubmit} className="space-y-4 mt-2">
                    <div className="space-y-2">
                        <Label htmlFor="nama">Nama Satuan <span className="text-red-500">*</span></Label>
                        <Input 
                            id="nama" 
                            placeholder="Contoh: Kilogram / Porsi / Pcs" 
                            value={formData.nama_satuan}
                            // FIX: Gunakan (prev: any) agar typescript tidak error implisit any
                            onChange={(e) => setFormData((prev: any) => ({...prev, nama_satuan: e.target.value}))}
                            required
                        />
                    </div>

                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                            Simpan
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}