'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';

// Definisi tipe data Satuan di sini atau import dari global types jika ada
export interface Satuan {
    id: number;
    nama_satuan: string;
}

export function useSatuanPage() {
    // State Data
    const [satuans, setSatuans] = useState<Satuan[]>([]);
    const [loading, setLoading] = useState(true);
    
    // Modal & Form State
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);
    const [formData, setFormData] = useState({
        nama_satuan: '',
    });

    // 1. Fetch Data
    const fetchSatuans = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/satuans');
            setSatuans(response.data);
        } catch (error) {
            console.error("Error fetching data:", error);
            toast.error("Gagal memuat data satuan.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchSatuans();
    }, []);

    // 2. Handlers
    const resetForm = () => {
        setFormData({ nama_satuan: '' });
        setEditId(null);
        setIsDialogOpen(false);
    };

    const handleOpenDialog = (satuan?: Satuan) => {
        if (satuan) {
            setEditId(satuan.id);
            setFormData({
                nama_satuan: satuan.nama_satuan,
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    const handleSubmit = async (e?: FormEvent) => {
        if (e) e.preventDefault();
        setIsSaving(true);

        try {
            if (editId) {
                // UPDATE
                await axios.put(`/api/satuans/${editId}`, formData);
                toast.success("Satuan berhasil diperbarui!");
            } else {
                // CREATE
                await axios.post('/api/satuans', formData);
                toast.success("Satuan berhasil ditambahkan!");
            }

            resetForm();
            fetchSatuans();
            
        } catch (error: any) {
            const pesan = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(pesan);
        } finally {
            setIsSaving(false);
        }
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin ingin menghapus satuan ini?")) return;
        try {
            await axios.delete(`/api/satuans/${id}`);
            toast.success("Satuan berhasil dihapus");
            fetchSatuans();
        } catch (error) {
            toast.error("Gagal menghapus satuan (Mungkin sedang digunakan oleh produk).");
        }
    };

    return {
        // Data
        satuans,
        loading,
        
        // State Form/Modal
        isDialogOpen,
        setIsDialogOpen,
        isSaving,
        editId,
        formData,
        setFormData,

        // Actions
        handleOpenDialog,
        handleSubmit,
        handleDelete
    };
}