'use client';

import { Pencil, Trash2, Loader2, Scale } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table, TableBody, TableCell, TableHead, TableHeader, TableRow,
} from '@/components/ui/table';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Satuan } from '@/hooks/admin/useSatuanPage'; // Import tipe data dari hook atau global types

interface SatuanTableProps {
    loading: boolean;
    data: Satuan[];
    onEdit: (item: Satuan) => void;
    onDelete: (id: number) => void;
}

export default function SatuanTable({
    loading,
    data,
    onEdit,
    onDelete
}: SatuanTableProps) {
    
    return (
        <Card>
            <CardHeader className="pb-3">
                <CardTitle className="text-lg">Daftar Satuan Unit</CardTitle>
            </CardHeader>
            <CardContent>
                <div className="rounded-md border">
                    <Table>
                        <TableHeader>
                            <TableRow className="bg-gray-50">
                                <TableHead className="w-[50px] text-center">No</TableHead>
                                <TableHead>Nama Satuan</TableHead>
                                <TableHead className="text-right">Aksi</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {loading ? (
                                <TableRow>
                                    <TableCell colSpan={3} className="h-24 text-center text-gray-500">
                                        <div className="flex justify-center items-center gap-2">
                                            <Loader2 className="h-5 w-5 animate-spin" /> Memuat data...
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) : data.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={3} className="h-24 text-center text-gray-500">
                                        Belum ada data satuan.
                                    </TableCell>
                                </TableRow>
                            ) : (
                                data.map((item, index) => (
                                    <TableRow key={item.id} className="hover:bg-gray-50">
                                        <TableCell className="text-center text-gray-500">{index + 1}</TableCell>
                                        <TableCell className="font-medium text-gray-900">
                                            <div className="flex items-center gap-2">
                                                <Scale className="w-4 h-4 text-gray-400" />
                                                {item.nama_satuan}
                                            </div>
                                        </TableCell>
                                        <TableCell className="text-right">
                                            <div className="flex justify-end gap-2">
                                                <Button 
                                                    variant="ghost" size="icon" 
                                                    className="h-8 w-8 text-blue-600 hover:bg-blue-50"
                                                    onClick={() => onEdit(item)}
                                                >
                                                    <Pencil className="h-4 w-4" />
                                                </Button>
                                                <Button 
                                                    variant="ghost" size="icon" 
                                                    className="h-8 w-8 text-red-600 hover:bg-red-50"
                                                    onClick={() => onDelete(item.id)}
                                                >
                                                    <Trash2 className="h-4 w-4" />
                                                </Button>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </div>
            </CardContent>
        </Card>
    );
}