'use client';

import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

// Import Custom Hook
import { useSatuanPage } from '@/hooks/admin/useSatuanPage';

// Import Komponen UI
import SatuanTable from '@/components/admin/satuan/SatuanTable';
import SatuanFormDialog from '@/components/admin/satuan/SatuanFormDialog';

export default function SatuanPage() {
    const {
        // Data
        satuans,
        loading,
        
        // State Form/Modal
        isDialogOpen,
        setIsDialogOpen,
        isSaving,
        editId,
        formData,
        setFormData,

        // Actions
        handleOpenDialog,
        handleSubmit,
        handleDelete
    } = useSatuanPage();

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Satuan</h1>
                    <p className="text-gray-500 text-sm">Atur satuan pengukuran (Kg, Pcs, Porsi, dll).</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b] shadow-sm"
                    onClick={() => handleOpenDialog()}
                >
                    <Plus className="w-4 h-4 mr-2" />
                    Tambah Satuan
                </Button>
            </div>

            {/* Tabel Data */}
            <SatuanTable 
                loading={loading}
                data={satuans}
                onEdit={handleOpenDialog}
                onDelete={handleDelete}
            />

            {/* Modal Dialog */}
            <SatuanFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                isSaving={isSaving}
                formData={formData}
                setFormData={setFormData}
                isEdit={!!editId}
            />
        </div>
    );
}